
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapGetters } from "vuex";
import api from "../../api/index.js";
export default {
components: {
XHeader,
Group,
Cell,
Badge
},
computed: {
...mapGetters(["newsLists"])
},
created() {


},
activated() {
let userid = window.localStorage.getItem("user");
this.$store.dispatch("getNewsList", { user_id: userid });
},
watch: {
newsLists(Value) {
this.unreadInfos = Value.unread;
this.readInfos = Value.read;
}
},
data() {
return {
noData: false,
unreadInfos: [
{
content: "当月走访任务没完成",
create_time: "2018-7-15"
},
{
content: "关于端午放假通知",
create_time: "2018-6-12"
}
],
readInfos: []
};
},
methods: {
reader(item) {
api.post_news_read({ message_id: item.id }).then(res => {
this.$router.push({path: "/home/help/add"});
});
}
}
};



